<template>        
    <div id="1" class="card" style="width: 60%; margin: 0 auto; padding: 30px 30px;">
        <h3>13. 理賠事由為何 ?</h3>
    </div>
    <br>
    <div class="card" style="width: 60%; margin: 0 auto; padding: 30px 30px;">
        <div class="card-body">
            <div v-if="showList === true">
                <div v-for="(item, index) in diseaseList" :key="index" class="form-check form-check-inline form-control-lg">
                    <input class="form-check-input" type="checkbox" :id="index" :value="item" v-model="result">
                    <label class="form-check-label" :for="index">{{item}}</label>
                </div>
                <br>
                <button @click="press(1)" class="btn btn-outline-success" type="button">
                    <b>確認</b>
                </button>     
            </div>
            <br>
            <div>
            <button @click="press(0)" class="btn btn-outline-dark" style="width:100%; padding: 10px 30px">
                <b>以上皆無</b>
            </button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data (){
        var disease = this.$route.params.disease;
        var diseaseList = [];
        var showList = false;
        var diabetesList = ['酮酸血症',
                        '高血糖高滲透壓症',
                        '口腔病變',
                        '腎臟病變',
                        '視網膜病變',
                        '神經病變',
                        '中風',
                        '高血壓',
                        '心肌梗塞'];
        var hypertensionList = ['心血管疾病',
                        '腦中風',
                        '糖尿病',
                        '腎臟病',
                        '出血性腦中風',
                        '缺血性腦中風',
                        '蜘蛛膜下出血腦中風',
                        '高血壓性腦病變',
                        '腦血管硬化狹窄',
                        '頸動脈硬化狹窄',
                        '急性心肌梗塞',
                        '急性肺水腫',
                        '急性心臟衰竭',
                        '急性腎衰竭',
                        '眼球內出血',
                        '視神經乳突水腫',
                        '主動脈剝離',
                        '剝離性主動脈瘤'];
        var myocardialInfarction = ['心律不整',
                        '心因性休克',
                        '肺水腫',
                        '心臟衰竭',
                        '心包膜積水',
                        '心臟破裂',
                        '心室壁瘤',
                        '二尖瓣脫垂',
                        '低血壓',
                        '猝死'];
        var bronchitisList = ['肺炎',
                        '中耳炎',
                        '鼻竇炎',
                        '阻塞性肺氣腫',
                        '支氣管肺炎',
                        '支氣管擴張'];
        var epidermoidCyst = ['身體組織變形',
                        '蜂窩性組織炎',
                        '敗血症',
                        '菌血症',
                        '筋膜炎',
                        '惡性腫瘤',
                        '皮膚癌'];
        var largeIntestine = ['大腸癌',
                        '腸阻塞',
                        '腸穿孔',
                        '惡性腫瘤'];
        if (disease.includes('糖尿病')){
            diseaseList.push.apply(diseaseList, diabetesList);
            showList = true;
        }
        if (disease.includes('高血壓')){
            diseaseList.push.apply(diseaseList, hypertensionList);
            showList = true;
        }
        if (disease.includes('心肌梗塞')){
            diseaseList.push.apply(diseaseList, myocardialInfarction);
            showList = true;
        }
        if (disease.includes('支氣管炎')){
            diseaseList.push.apply(diseaseList, bronchitisList);
            showList = true;
        }
        if (disease.includes('粉瘤')){
            diseaseList.push.apply(diseaseList, epidermoidCyst);
            showList = true;
        }
        if (disease.includes('大腸瘜肉')){
            diseaseList.push.apply(diseaseList, largeIntestine);
            showList = true;
        }
        
        return {
            showList,
            diseaseList,
            result: []
        }
    },
    methods: {
        press: function(input){
            if(input == 0){
                this.$router.push({ name: 'result4'});
            }
            else if(input == 1 && this.result.length == 0){
                alert("請至少勾選一項疾病");
            }
            else{
                this.$router.push({ 
                    name: '14',
                    params:{
                        name: this.$route.params.name,
                        type: this.$route.params.type,
                        samePeople: this.$route.params.samePeople,
                        insurerCareer: this.$route.params.insurerCareer,
                        insuredCareer: this.$route.params.insuredCareer,
                        insurerDate: this.$route.params.insurerDate,
                        serviceDate: this.$route.params.serviceDate,
                        disease: this.$route.params.disease,
                        sufferTiming: this.$route.params.sufferTiming,
                        notInformedReason: this.$route.params.notInformedReason,
                        settlementReason: this.result
                    }
                });
            }
        }
    }
};
</script>